/*  BURGER  */
.menu-wrap {
  @include mobile {
    display: block;
    position: relative;
  }
  @include laptop {
    display: none;
  }
}
#hamburger {
  border: none;
  background: none;
  cursor: pointer;
  z-index: 9999999999;
  @include mobile {
    position: fixed;
    right: 20px;
    top: 40px;
  }
  @include tablet {
    position: fixed;
    top: 40px;
    cursor: pointer;
  }
}

.menu-btn {
  @include mobile {
    width: 30px;
    height: 30px;
  }
  @include tablet {
    width: 30px;
    height: 30px;
  }
  @include laptop {
    display: none;
  }
}
.menu-btn span {
  width: 24px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: $white;
  transition: all 0.5s;
}
.menu-btn.active span {
  background-color: $primary;
}

.menu-btn span:nth-of-type(2) {
  top: calc(40% - 5px);
}
.menu-btn span:nth-of-type(3) {
  top: calc(60% + 5px);
}
.menu-btn span:nth-of-type(4) {
  top: calc(60% + 5px);
}

#hamburger:focus {
  outline: none;
}

.menu-container {
  padding: 32px 32px;
  width: 100%;

  @include tablet {
    width: auto;
  }
}
.menu {
  position: absolute;
  left: 100%;
  top: 0;
  visibility: hidden;
  width: 100vw;
  height: 100vh;
  backdrop-filter: blur(2px);
  transform: translateX(-100%);
  transition: transform 0.5s;
  -webkit-transform: translateX(-100%);
  -moz-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
}
.menu.active {
  visibility: visible;
  left: 0;
  width: 100%;
  position: fixed;
  height: 100%;
  z-index: 99999999;
  display: flex;
  transition: height 1s 1s linear;
  transform: translateX(0%);
  -webkit-transform: translateX(0%);
  -moz-transform: translateX(0%);
  -ms-transform: translateX(0%);
  -o-transform: translateX(0%);
  margin-top: 40px;
  background: #ffffffdb;

  @include tablet {
    margin-top: 0px;
    left: 50%;
  }
}

// Changing the hamburger icon when the menu is open

.menu-btn.active span:nth-of-type(1) {
  display: none;
}
.menu-btn.active span:nth-of-type(2) {
  top: 50%;
  transform: translate(-50%, 0%) rotate(45deg);
}
.menu-btn.active span:nth-of-type(3) {
  top: 50%;
  transform: translate(-50%, 0%) rotate(-45deg);
}

// menu-link

.menu-link {
  display: flex;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
  font-style: oblique;
  color: $primary;
  justify-content: flex-start;
  margin-top: 16px;
  margin-left: 32px;
  font-family: "Unbounded";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 1.5;
  transition: all 0.6ms ease-in;
}
.menu-link:hover,
.menu-link:focus {
  color: $accent;
}

.white {
  background: $background;
}

.menu-item:nth-child(10) {
  margin-bottom: 16px;
}

.mobile-title,
.mobile-subtitle {
  font-family: "Unbounded";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.2;
  color: $primary;

  &:hover,
  &:focus {
    color: $accent;
  }
}

.mobile-subtitle {
  margin-top: 30px;
}

.mobile-social-media {
  margin-top: 34px;
  display: flex;
  &__item {
    margin-right: 26px;
    width: 24px;
    height: 24px;
    transition: all 0.6ms ease-in;
    &:hover,
    &:focus {
      fill: $accent;
      color: $accent;
    }
  }
}
